import React from "react";

export default function About() {
  return (
    <>
      <div id="About">
        <div className="container">
          <h2>ABOUT</h2>
          <div className="about-line">
            <img src="images/about.png" alt="Me" width="150px" />
            <h3>Inspiring full-stack, improving one at a time.</h3>
            <p>
              Started my career as a front-end web developer during student
              internship back in college. Shifted my career to application
              configuration analyst where it didn't only tested my skills as a
              web developer but also communication and problem-solving skills.
              After one year of being an analyst, I went back to being front-end
              web developer in Software Developer title. As I indulge myself
              with helping the back-end support, I decided to take my career to
              be a full-stack developer.
            </p>
          </div>
        </div>
      </div>
    </>
  );
}
